import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
// import reportWebVitals from "./reportWebVitals";

import { Provider } from "react-redux";
import { store } from "./redux/store";

import "./styles/App.css";
import "./styles/chat-widget.css";

// Custom JS
import "./panel";

// Helpers
import waitForElement from "./helpers/waitForElement";

// Roots
import ErrorMessages from "./components/error-messages";
import SupportButton from "./components/support-button";

// App
const root = ReactDOM.createRoot(document.getElementById("img_root"));

root.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>
);

waitForElement("#app:not(.loading)").then(() => {
  window.img_state = window?.app?.__vue__?.$store?.state;
  console.log("IMG - App is no longer loading. Here is the state.", img_state);

  // Error Messages
  waitForElement(
    "#app > div.flex.v2-open.sidebar-v2-location.flex.v2-open.sidebar-v2-location > div:nth-child(2) > header"
  ).then(() => {
    let header = document.querySelector(
      "#app > div.flex.v2-open.sidebar-v2-location.flex.v2-open.sidebar-v2-location > div:nth-child(2) > header"
    );
    let body = document.querySelector(
      "#app > div.flex.v2-open.sidebar-v2-location.flex.v2-open.sidebar-v2-location > section > div > section > section"
    );

    let img_error_root = document.createElement("div");

    img_error_root.id = "img_error_root";

    body.prepend(img_error_root);

    img_error_root = ReactDOM.createRoot(
      document.getElementById("img_error_root")
    );

    img_error_root.render(
      <React.StrictMode>
        <Provider store={store}>
          <ErrorMessages />
        </Provider>
      </React.StrictMode>
    );
  });

  // Support Button
  let avatar = document.querySelector(".hl_header--avatar");
  let container = document.createElement("div");
  container.id = "img_nav_root";
  avatar.parentElement.insertAdjacentElement("beforebegin", container);
  const root = ReactDOM.createRoot(document.getElementById("img_nav_root"));

  root.render(
    <React.StrictMode>
      <SupportButton />
    </React.StrictMode>
  );
});

